import React from "react";

function SettingsColor() {
    const [backgroundColor1, setBackgroundColor1] = React.useState(localStorage.getItem("background-color-1") || "#0061FE");
    const [backgroundColor2, setBackgroundColor2] = React.useState(localStorage.getItem("background-color-2") || "#01C7FC");
    const [backgroundColor3, setBackgroundColor3] = React.useState(localStorage.getItem("background-color-3") || "#006AB7");

    function saveColor(e) {
        localStorage.setItem(e.target.id, e.target.value);
        if (e.target.id === "background-color-1") {
            setBackgroundColor1(e.target.value);
            document.documentElement.style.setProperty("--background-color-1", localStorage.getItem("background-color-1"));
            document.querySelector('meta[name="theme-color"]').setAttribute("content", localStorage.getItem("background-color-1"));
        } else if (e.target.id === "background-color-2") {
            setBackgroundColor2(e.target.value);
            document.documentElement.style.setProperty("--background-color-2", localStorage.getItem("background-color-2"));
        } else if (e.target.id === "background-color-3") {
            setBackgroundColor3(e.target.value);
            document.documentElement.style.setProperty("--background-color-3", localStorage.getItem("background-color-3"));
        }
    }

    return (
        <div className="col-12">
            <div className="tile--coloured p-3 px-md-5 text-center">
                <p className="text text--md text--white fw-bold spacing--10 mb-3"> Farbschema</p>
                <div className="d-flex justify-content-around flex-wrap">
                    <div className="form-group text-start text-center">
                        <input type="color" className="form-control form-control text--sm" id="background-color-1" value={backgroundColor1} onChange={(e) => saveColor(e)} />
                    </div>
                    <div className="form-group text-start text-center">
                        <input type="color" className="form-control form-control text--sm" id="background-color-2" value={backgroundColor2} onChange={(e) => saveColor(e)} />
                    </div>
                    <div className="form-group text-start text-center">
                        <input type="color" className="form-control form-control text--sm" id="background-color-3" value={backgroundColor3} onChange={(e) => saveColor(e)} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SettingsColor;
