import React from "react";
import StatisticsNumber from "./StatisticsNumber";
import StatisticsRight from "./StatisticsRight";
import StatisticsEvent from "./StatisticsEvent";
import useFetch from "../../helper/useFetch";

function Statistics() {
    const { data, loading, error } = useFetch("stats", undefined, localStorage.getItem("eid"));

    const formattedRights = data?.rights.map((item) => {
        return <StatisticsRight key={item.key} item={item.data} />;
    });

    if (loading)
        return (
            <div className="text-center m-5">
                <div className="spinner-border text-center" role="status">
                    <span className="sr-only"></span>
                </div>
            </div>
        );

    if (error) console.log(error);

    return (
        <div className="row m-2 py-2 m-md-5">
            <div className="container col-md-6">
                <div className="row gx-3 gy-3">
                    <StatisticsNumber number={data?.checkedInAttendees} description="Teilnehmer eingecheckt" />
                    <StatisticsNumber number={data?.allAttendees} description="Teilnehmer registriert" />
                    <div className="col-12">
                        <div className="tile--grey">{formattedRights}</div>
                    </div>
                </div>
            </div>
            <div className="col-md-6">{data?.title && <StatisticsEvent data={data} />}</div>
        </div>
    );
}

export default Statistics;
