import React, { useState } from "react";
import HomeAttendeeInfo from "./HomeAttendeeInfo";
import HomeCheckinTile from "./HomeCheckinTile";
import HomePrintTile from "./HomePrintTile";
import HomeWarning from "./HomeWarning";
import * as Zebra from "../../print/zebra";
import useGetAttendee from "../../helper/useGetAttendee";
import { validateRight } from "../../utils/utils";
import QrReaderInput from "./QrReader";
import printJS from "print-js";
import axios from "axios";
import useFetch from "../../helper/useFetch";

function Home(props) {
    const { refetch } = useFetch(undefined);
    const [hash, attendee, loading, error, setHash] = useGetAttendee();

    const [warning, setWarning] = useState({});

    const printElements = attendee.data.customDocuments.map((item, index) => {
        return <HomePrintTile key={"custom" + index} item={item} id={"custom" + index} />;
    });

    const checkinElements = attendee.rights?.map((item) => {
        return <HomeCheckinTile key={item.TidPid} item={item} attendee={attendee} />;
    });

    React.useEffect(() => {
        if (error) {
            setWarning({ icon: "error", text: error });
        }
    }, [error]);

    // Auto Checkin
    React.useEffect(() => {
        if (JSON.parse(localStorage.getItem("autoCheckinActivated"))) {
            var validRights = attendee.rights?.filter((item) => {
                return validateRight(item, attendee.data.checkIns);
            });

            if (validRights.length === 0) {
                setWarning({ icon: "error", text: "Teilnehmer ist nicht berechtigt" });
            } else if (validRights.length === 1) {
                refetch("attendeeCheckin", attendee.ticketHash, validRights[0].TidPid);
                setWarning({ icon: "success", text: "Teilnehmer wurde erfolgreich eingecheckt" });
            } else if (validRights.length > 0) {
                setWarning({ icon: "warning", text: "Es gibt mehrere gültige Checkin Rechte. Wähle das passende manuell aus." });
            }
        }

        console.log("Check for Auto Badging");
        console.log(JSON.parse(localStorage.getItem("autoBadgePrintActivated")) && attendee.data.defaultBadgePrinted !== true && attendee.data.defaultBadgeURL);
        if (JSON.parse(localStorage.getItem("autoBadgePrintActivated")) && attendee.data.defaultBadgePrinted !== true && attendee.data.defaultBadgeURL) {
            console.log("Print: " + attendee.data.defaultBadgeURL);
            if (JSON.parse(localStorage.getItem("zebraActivated"))) {
                Zebra.sendImage(attendee.data.defaultBadgeURL);
                const baseURL = "https://hook.doo.integromat.celonis.com/" + localStorage.getItem("webhookURL");
                axios
                    .get(`${baseURL}?request=defaultBadgePrinted&hash=${attendee.hash}`)
                    .then((response) => console.log(response))
                    .catch((err) => console.log(err));
            } else {
                printJS({ printable: attendee.data.defaultBadgeURL, type: "image" });
            }
        }
        // eslint-disable-next-line
    }, [attendee]);

    function processInput(input) {
        console.log("Process input: " + input);
        if (input === "/demo" || input === "/Demo") {
            localStorage.setItem("webhookURL", "un70t4or2i0yncuipi5qj6g80av1uej1");
            localStorage.setItem("oid", "15382");
            localStorage.setItem("eid", "122078");
            setWarning({ icon: "success", text: "Demo wurde aktiviert!" });
        } else if (input.startsWith("config:")) {
            console.log("Set connection settings");
            var obj = JSON.parse(input.substring(7));
            localStorage.setItem("webhookURL", obj.webhookURL);
            localStorage.setItem("oid", obj.oid);
            localStorage.setItem("eid", obj.eid);
            setWarning({ icon: "success", text: "Verbindungsdaten wurden erfolgreich hinterlegt!" });
        } else if (input === "/zebra") {
            localStorage.setItem("zebraActivated", "true");
            Zebra.setup();
            setWarning({ icon: "success", text: "Zebra-Drucker wurde aktiviert!" });
        } else if (input === "/zebra-stop") {
            localStorage.setItem("zebraActivated", "false");
            setWarning({ icon: "success", text: "Zebra-Drucker wurde deaktiviert" });
        } else if (input === "/reset-background") {
            console.log("Reset Background");
            localStorage.removeItem("background-color-1");
            localStorage.removeItem("background-color-2");
            localStorage.removeItem("background-color-3");
        } else if (!input.startsWith("/")) {
            setWarning({});
            setHash(input.slice(-8));
        }
    }

    const scannerInput = (event) => {
        console.log("A key was pressed", event.keyCode);
    };

    //Honeywell scanner input
    React.useEffect(() => {
        var scanInput = "";
        var scanActive = false;

        window.addEventListener("keydown", function (e) {
            const textInput = e.key || String.fromCharCode(e.keyCode);
            const targetName = e.target.localName;
            if (textInput === "!") {
                scanActive = true;
            } else if (textInput === "?") {
                scanActive = false;
                console.log("New Scan: " + scanInput);
                setWarning({});
                setHash(scanInput.slice(-8));

                scanInput = "";
            } else if (scanActive && textInput && textInput.length === 1 && targetName !== "input") {
                scanInput = scanInput + textInput;
            }
        });

        // cleanup this component
        return () => {
            window.removeEventListener("keydown", scannerInput);
        };
    }, [setHash]);

    return (
        <div>
            <div className="row m-2 m-md-5">
                <div className="col-md-5">
                    <div className="mobile--tile mb-3">
                        {warning.text && warning.icon && <HomeWarning icon={warning.icon} text={warning.text} setWarning={setWarning} />}
                        {JSON.parse(localStorage.getItem("QrCodeActivated")) && <QrReaderInput processInput={processInput} />}
                        <HomeAttendeeInfo ticketHash={hash} processInput={processInput} attendee={attendee?.data} loading={loading} />
                    </div>
                </div>
                <div className="col-md-7">
                    <div className="tile--grey mb-3">
                        {JSON.parse(localStorage.getItem("autoCheckinActivated")) && (
                            <div className="d-flex align-items-center justify-content-end status-container">
                                <p className="pe-1 m-0 text text--xs text--white fw-light">Auto Checkin</p>
                                <div className="green--marker"></div>
                            </div>
                        )}
                        {checkinElements}
                    </div>
                    <div className="tile--grey mb-2">
                        {JSON.parse(localStorage.getItem("autoBadgePrintActivated")) && (
                            <div className="d-flex align-items-center justify-content-end status-container">
                                <p className="pe-1 m-0 text text--xs text--white fw-light">Auto Badging</p>
                                <div className="green--marker"></div>
                            </div>
                        )}
                        {JSON.parse(localStorage.getItem("zebraActivated")) && (
                            <div className="d-flex align-items-center justify-content-end status-container">
                                <p className="pe-1 m-0 text text--xs text--white fw-light">Zebra</p>
                                <div className="green--marker"></div>
                            </div>
                        )}
                        {attendee.data.defaultBadgeURL && <HomePrintTile key="defaultBadge" item={{ name: "Badge", url: attendee.data.defaultBadgeURL, type: "defaultBadge", hash: hash, printed: attendee.data.defaultBadgePrinted }} id={"defaultBadge"} />}
                        {printElements}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Home;
