import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck, faCircleExclamation, faCircleXmark } from "@fortawesome/free-solid-svg-icons";

function HomeWarning(props) {
    var color;
    var icon;

    if (props.icon === "success") {
        icon = <FontAwesomeIcon className="m-1 text--white text--xxl" icon={faCircleCheck} />;
        color = "background--green";
    } else if (props.icon === "warning") {
        icon = <FontAwesomeIcon className="m-1 text--white text--xxl" icon={faCircleExclamation} />;
        color = "background--orange";
    } else if (props.icon === "error") {
        icon = <FontAwesomeIcon className="m-1 text--white text--xxl" icon={faCircleXmark} />;
        color = "background--red";
    }

    return (
        <div className={"mb-3 text--md text--white spacing--10 d-flex justify-content-between tile--outline " + color}>
            <div className="d-flex align-items-center">
                {icon}
                <span className="ms-1">{props.text}</span>
            </div>

            <span>
                <button onClick={() => props.setWarning("")} className="btn-close btn-close-white"></button>
            </span>
        </div>
    );
}

export default HomeWarning;
