import React from "react";
import * as utils from "../../utils/utils";
import useCheckin from "../../helper/useCheckin";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";

function HomeCheckinTile(props) {
    const { data, loading, error, checkin, reset } = useCheckin({ hash: props.attendee.ticketHash, identifier: props.item.TidPid });

    React.useEffect(() => {
        reset();
        // eslint-disable-next-line
    }, [props.attendee.ticketHash]);

    const count = utils.countEntries(props.item.TidPid, props.attendee.data.checkIns);
    const rightValid = utils.validateRight(props.item, props.attendee.data.checkIns);
    const timeFramesCount = props.item.timeFrames.length;

    return (
        <div id={props.item.TidPid} className={"mx-2 my-4 m-md-4 d-flex align-items-center flex-wrap justify-content-center " + (rightValid ? "" : "not-available")}>
            <div className="text text--white flex-grow-1 text-center text-xl-start">
                <p className="m-0 d-flex flex-wrap align-items-center justify-content-center justify-content-xl-start">
                    <span className="text--lg">{props.item.Name}</span>
                    {(count > 0 || data?.status === "success") && <span className="green-tag m-1">Eingecheckt</span>}
                </p>
                <p className="text--sm fw-light m-0">
                    {count}/{props.item.maxEntries} {props.item.maxEntries === 1 ? "Eintritt" : "Eintritte"} | {timeFramesCount} {timeFramesCount === 1 ? "Zeitraum" : "Zeiträume"} hinterlegt
                </p>
                {error && (
                    <p className="text--red fw-bold">
                        <span>{error}</span>
                    </p>
                )}
            </div>
            <button disabled={!rightValid || data?.status === "success" ? "disabled" : ""} onClick={() => checkin(props.item.TidPid)} className="m-2 btn--secondary btn--lg">
                {loading ? <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> : <span className="text-uppercase spacing--10">{data?.status === "success" ? <FontAwesomeIcon className="" icon={faCheck} /> : "Einchecken"}</span>}
            </button>
        </div>
    );
}

export default HomeCheckinTile;
