import { useState } from "react";
import axios from "axios";

function useCheckin(input) {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    function reset() {
        setData(null);
        setError(null);
    }

    function checkin(tidPid) {
        setLoading(true);
        var url = `https://hook.doo.integromat.celonis.com/${localStorage.getItem("webhookURL")}?request=checkinAttendee&hash=${input.hash}&identifier=${tidPid}`;
        console.log("Checkin attendee: " + url);

        axios
            .get(url)
            .then((response) => {
                setData(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                setError(err);
                console.log(err);
            })
            .finally(() => {
                setLoading(false);
            });
    }

    return { data, loading, error, checkin, reset };
}

export default useCheckin;
