import React from "react";

function SettingsConnection(props) {
    const [webhookUrl, setWebhookUrl] = React.useState(localStorage.getItem("webhookURL") || "");
    const [eid, setEid] = React.useState(localStorage.getItem("eid") || "");

    function saveInput(e) {
        localStorage.setItem(e.target.id, e.target.value);
        if (e.target.id === "webhookURL") {
            setWebhookUrl(e.target.value);
        } else if (e.target.id === "eid") {
            setEid(e.target.value);
        }
    }

    //props.setSettingsWarning(webhookUrl ? false : true);

    return (
        <div className="col-12">
            <div className="tile--grey p-3 px-md-5">
                <div className="text text-center">
                    <p className="text--md text--white fw-bold spacing--10 m-0">Verbindungseinstellungen</p>
                    {!webhookUrl && (
                        <div className="alert alert-warning" role="alert">
                            Es muss ein Verbindungsschlüssel hinterlegt sein, damit Tickets entwertet werden können!
                        </div>
                    )}
                    <p className="text--secondary text--sm">Der Verbindungsschlüssel ist notwendig, um Tickets einchecken zu können. Die Event-ID sind für den Checkin optional, wird aber für die Statistik gebraucht.</p>
                </div>
                <div className="d-flex justify-content-around text text-left text--white">
                    <div className="form-group flex-grow-1 mx-2">
                        <label className="mb-0 text--sm" htmlFor="name">
                            Verbindungsschlüssel
                        </label>
                        <input type="text" className="input--text w-100" id="webhookURL" placeholder="" value={webhookUrl} onChange={(e) => saveInput(e)} />
                    </div>
                    <div className="form-group mx-2">
                        <label className="mb-0 text--sm" htmlFor="eid">
                            Event ID
                        </label>
                        <input type="text" className="input--text w-100" id="eid" placeholder="" value={eid} onChange={(e) => saveInput(e)} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SettingsConnection;
