import React from "react";
import { formatDate } from "../../utils/utils";

function StatisticsRight(props) {
    return (
        <div className="p-4">
            <div className="d-flex justify-content-between">
                <p className="p-0 m-0">
                    <a className="text text--white text-decoration-none" data-bs-toggle="collapse" href={"#collapse" + props.item.TidPid} role="button" aria-expanded="false" aria-controls={"collapse" + props.item.TidPid}>
                        <span className="spacing--10 fw-light">{props.item.Name}</span>
                    </a>
                </p>
                {props.item.active ? <p className="p-0 m-0 text--green">Aktiv</p> : <p className="p-0 m-0 text--secondary">Aktiv</p>}
            </div>
            <div className="collapse" id={"collapse" + props.item.TidPid}>
                <div className="tile--outline text text--white">
                    <div className="text text--sm text--secondary m-1">
                        <p className="text text--md text--white mb-0 mx-1 ">Zeitraum</p>
                        {props.item.timeFrames.map((item, index) => {
                            return (
                                <p key={"c" + index} className="text text--sm text--secondary m-0">
                                    {formatDate(item.validFrom)} - {formatDate(item.validTill)}
                                </p>
                            );
                        })}
                        <p className="text text--md text--white mb-0 mt-2 mx-1 ">Maximale Eintritte</p>
                        <p className="text text--sm text--secondary m-0">{props.item.maxEntries}</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default StatisticsRight;
