import React, { useRef } from "react";
import { formatDate } from "../../utils/utils";
import useFetch from "../../helper/useFetch";

function HomeAttendeeInfo(props) {
    const { data, refetch, setData } = useFetch(undefined);
    const [showSearch, setShowSearch] = React.useState(false);
    const inputRef = useRef();

    const formattedLogs = props.attendee.logs.map((item, index) => {
        return (
            <p key={index}>
                {formatDate(item.date)}: {item.description}
            </p>
        );
    });

    const formattedSearchResults = data?.results.map((item) => {
        return (
            <li
                key={item.key}
                onClick={(e) => {
                    props.processInput(item.key);
                    inputRef.current.value = "";
                    setShowSearch(false);
                    setData(null);
                }}
            >
                {item.key} - {item.data.firstName} {item.data.lastName}
            </li>
        );
    });

    async function search(e) {
        if (!e.target.value.startsWith("/") && e.target.value.length >= 3 && localStorage.getItem("webhookURL")) {
            console.log("Suche: " + e.target.value);
            refetch("search", undefined, e.target.value);
        } else {
            setData(null);
        }
    }

    function onSubmit(e) {
        e.preventDefault();
        props.processInput(inputRef.current.value);
        setData(null);
        inputRef.current.value = "";
    }

    return (
        <div className="p-3">
            {props.loading && (
                <div className="text-center">
                    <div className="spinner-border text-center" role="status">
                        <span className="sr-only"></span>
                    </div>
                </div>
            )}
            {!props.loading && (
                <div>
                    {showSearch ? (
                        <form onSubmit={onSubmit} className="">
                            <input
                                autoFocus
                                ref={inputRef}
                                onKeyUp={(e) => search(e)}
                                onBlur={() => {
                                    if (inputRef.current.value === "") {
                                        setShowSearch(false);
                                    }
                                }}
                                className="input--text me-1 w-100"
                                type="text"
                                placeholder="SUCHE"
                                name="search"
                            ></input>
                            <div id="result" className="m-0 pt-1">
                                <ul id="result-list">{formattedSearchResults}</ul>
                            </div>
                        </form>
                    ) : (
                        <h2 className="text--white d-flex justify-content-between align-items-start">
                            <span className="spacing--10 align-top text--xl">
                                {props.attendee.firstName} {props.attendee.lastName}
                            </span>
                            <button className="btn--icon me-xl-5" onClick={() => setShowSearch(true)}>
                                <div className="searchAttendeeLogo"></div>
                            </button>
                        </h2>
                    )}

                    <div className="spacing--10">
                        <div className="check-in__booking-id mt-4">
                            <p className="text text--sm text--white m-0 fw-light">
                                <span>Ticket-ID</span>
                            </p>
                            <p id="booking-id" className="text text--md text--white">
                                {props.ticketHash}
                            </p>
                        </div>
                        <div className="check-in__booking-id mt-4">
                            <p className="text text--sm text--white m-0 fw-light">
                                <span>Buchungsnummer</span>
                            </p>
                            <p id="booking-id" className="text text--md text--white">
                                {props.attendee.bookingNumber}
                            </p>
                        </div>
                        <div className="check-in__booking-event mt-4">
                            <p className="text text--sm text--white m-0 fw-light">
                                <span>Veranstaltung</span>
                            </p>
                            <p id="event" className="text text--md text--white">
                                {props.attendee.event}
                            </p>
                        </div>
                    </div>
                    <p className="m-0 mb-2 mt-4 mt-md-5">
                        <a className="text--white spacing-10" data-bs-toggle="collapse" href="#logs" role="button" aria-expanded="false" aria-controls="logs">
                            LOGS ANZEIGEN
                        </a>
                    </p>
                    <div className="collapse" id="logs">
                        <div className="tile--grey text text--white" id="logs-body">
                            {formattedLogs}
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default HomeAttendeeInfo;
