import React from "react";
import * as Zebra from "../../print/zebra";
import Select from "react-select";

function SettingsZebra() {
    return (
        <div className="col-12">
            <div className="tile--grey p-3 px-md-5">
                <div className="text text-center">
                    <p className="text--md text--white fw-bold spacing--10 m-0">Druckereinstellungen</p>
                    <p className="text--secondary text--sm">Erklärung Druckereinstellungen</p>
                </div>
                <div className="m-2">
                    <Select defaultValue={{ value: Zebra.selected_device?.uid, label: Zebra.selected_device?.name }} onChange={(e) => Zebra.setDevice(e.value)} options={Zebra.selectOptions} />
                </div>
                <div className="d-flex flex-wrap m-1 justify-content-between">
                    <button onClick={() => Zebra.printConfig()} className=" mx-1 btn--secondary btn--lg">
                        Konfiguration
                    </button>
                    <button onClick={() => Zebra.printTestLabel()} className="mx-1 btn--secondary btn--lg">
                        Testlabel
                    </button>
                    <button onClick={() => Zebra.setup()} className="mx-1 btn--secondary btn--lg">
                        Aktualisieren
                    </button>
                </div>
            </div>
        </div>
    );
}

export default SettingsZebra;
