import "bootstrap";
import React from "react";
import Navbar from "./components/Navbar";
import Home from "./components/Home/Home";
import Statistics from "./components/Statistics/Statistics";
import Settings from "./components/Settings/Settings";
import Footer from "./components/Footer";
import * as Zebra from "./print/zebra";

function App() {
    try {
        document.querySelector('meta[name="theme-color"]').setAttribute("content", localStorage.getItem("background-color-1"));
        document.documentElement.style.setProperty("--background-color-1", localStorage.getItem("background-color-1"));
        document.documentElement.style.setProperty("--background-color-2", localStorage.getItem("background-color-2"));
        document.documentElement.style.setProperty("--background-color-3", localStorage.getItem("background-color-3"));
    } catch (e) {
        console.log("Could not update color");
    }

    const [settingsWarning, setSettingsWarning] = React.useState(false);
    const [content, setContent] = React.useState("Home");

    React.useEffect(() => {
        if (JSON.parse(localStorage.getItem("zebraActivated"))) {
            Zebra.setup();
        }
    }, []);

    return (
        <div className="App">
            <Navbar content={content} setContent={setContent} settingsWarning={settingsWarning} />
            <div className="container content px-0">
                {content === "Home" && <Home />}
                {content === "Statistics" && <Statistics />}
                {content === "Settings" && <Settings setSettingsWarning={setSettingsWarning} />}
            </div>
            <div className="spacer--100"></div>
            <Footer />
        </div>
    );
}

export default App;
